.sidebar--layout {
  display: grid;
  grid-template-areas: 'header aside';
  grid-template-columns: 1fr auto;
  grid-template-rows: 1fr auto;
  margin-right: auto;
  height: auto;
  width: 100%;
}

.sidebar--shadow {
  box-shadow:
    16px 0 52px 0 rgba(14, 24, 80, 0.2),
    -2px 0 4px 0 rgba(69, 79, 126, 0.02);
}

.header-menu {
  grid-area: header;
  display: flex;
  flex-direction: column;
  padding-left: 10px;
}

.menu-layout {
  display: flex;
  padding: 15px;
  gap: 10px;
}

.services--menu {
  list-style: none;
  padding-left: 20px;
  padding-top: 10px;
  max-height: calc(100vh - 300px);
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.services--menu::-webkit-scrollbar {
  display: none;
}

.services__elem {
  margin-bottom: 30px;
}

.link {
  text-decoration: none;
  color: black;
}

.link:hover {
  color: #41f6d7;
}

.link--1 {
  color: #7e7e82;
}

.services__elem-mod {
  position: relative;
  cursor: pointer;
}

.services__elem-mod::before {
  content: '';
  position: absolute;
  top: 50%;
  left: -15px;
  transform: translateY(-50%);
  width: 4px;
  height: 35px;
  background: #41f6d7;
  border-radius: 0 4px 4px 0;
}

.menu-nav--layout {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.contacts--mod {
  margin-left: 15px;
}

.call-button {
  padding-left: 0;
}

.lang {
  padding: 15px 0 15px 0;
}

.search--modificate {
  grid-area: aside;
}

.button-side {
  background-color: white;
  border: none;
  cursor: pointer;
}

.search-button {
  padding: 19px;
}

.phone {
  font-weight: 900;
}

.lang_elem {
  color: #7e7e82;
  margin-right: 5px;
}

@media (min-width: 769px) {
  .sidebar--layout {
    max-width: 320px;
  }
}

@media (min-width: 1440px) {
  .sidebar--layout {
    max-width: 320px;
  }

  .sidebar--shadow {
    box-shadow: none;
  }

  .contacts-button {
    display: flex;
  }

  .close {
    display: none;
  }
}
