$border: 1px solid #ccc;
$font-stack: 'TT-Lakes', sans-serif;

@mixin button {
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
  width: 40px;
}

@mixin button-3 {
  padding: 10px;
  width: 119px;
  height: 32px;
  background-color: #ff3e79;
  align-items: center;
  border-radius: 16px;
  position: absolute;
  bottom: 15px;
  right: 15px;
}

@mixin icons-layout {
  display: flex;
  justify-content: space-evenly;
  gap: 15px;
}

@mixin divider {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 2px;
  height: 32px;
  background: #eaeaea;
  border-radius: 1px;
}

body {
  font-family: 'TT-Lakes', sans-serif;
  min-width: 320px;
  margin: 0;

  .sprite-hidden {
    display: none;
  }

  @media (min-width: 769px) {
    min-width: 769px;
  }

  @media (min-width: 1440px) {
    min-width: 1440px;
    max-width: 1440px;
    display: grid;
    grid-template-areas: 'header main' 'header footer';
    margin: 0 auto;
    height: auto;
  }
}

.header--layout {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 10px;

  @media (min-width: 769px) {
    padding: 0 15px 0 15px;
  }

  @media (min-width: 1440px) {
    display: none;
  }

  .menu-button {
    @include button;

    @media (min-width: 769px) {
      margin-right: 15px;
    }
  }
  .icons {
    display: flex;
    position: relative;
    padding-right: 16px;

    .icons-button {
      @include button;
    }

    @media (min-width: 769px) {
      justify-content: space-between;
      flex-grow: 10;
      align-items: center;
      padding-left: 15px;
    }

    &:before {
      @include divider;
      content: none;
      left: 0;

      @media (min-width: 769px) {
        content: '';
      }
    }

    &:after {
      @include divider;
      right: 0;
    }

    .icons-support {
      display: none;

      @media (min-width: 769px) {
        display: flex;
        justify-content: space-between;
        gap: 15px;
      }
    }

    .icons-info {
      &__element {
        align-self: flex-start;
        width: 97px;
        padding-left: 15px;

        @media (min-width: 769px) {
          padding-left: 0;
        }
      }
    }
  }
  .settings-icons {
    display: flex;
    margin-left: auto;
    gap: 20px;

    @media (min-width: 769px) {
      padding-left: 14px;
    }

    &__button {
      @include button;
    }
  }
}

.sidebar {
  grid-area: header;
  align-self: start;
  height: 100vh;
  background-color: #ffffff;
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  transform: translateX(-100%);
  transition: transform 1s;

  @media (min-width: 1440px) {
    transform: translateX(0);
    display: grid;
    position: sticky;
  }
}

.sidebar--open {
  transform: translateX(0);
}

.main {
  background-color: #f8f8f8;
  border-top-style: solid;
  border-top-width: 2px;
  border-top-color: #d9fff5;
  grid-area: main;

  .header2 {
    &__element {
      position: relative;
      margin-left: 15px;
      font-weight: 900;
      line-height: 40px;

      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: -15px;
        transform: translateY(-50%);
        width: 4px;
        height: 40px;
        background: #41f6d7;
        border-radius: 0 4px 4px 0;
      }

      @media (min-width: 1440px) {
        margin-right: auto;
      }
    }

    @media (min-width: 1440px) {
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
  }

  .settings-icons {
    display: none;

    @media (min-width: 1440px) {
      display: inline-block;
    }

    &__button {
      display: inline-flex;
      align-items: center;
      gap: 8px;
      padding: 10px 16px;
      background: none;
      border: none;
      cursor: pointer;
      font-size: 16px;
    }
  }

  .services {
    display: flex;
    max-height: 40px;
    overflow-x: auto;
    scrollbar-width: none;
    gap: 10px;
    margin-left: 15px;

    .services__button {
      margin: 5px 10px 5px 10px;
      padding: 5px;
      white-space: nowrap;
      background-color: #f8f8f8;
      border: none;
      font-size: 16px;
      color: #7e7e82;

      &:focus {
        border: solid;
        border-color: #41f6d7;
        border-width: 2px;
        border-radius: 6px;
        font-weight: 500;
        color: #7e7e82;
      }
    }
  }

  .info-reader {
    &__text-content {
      margin-left: 15px;
      margin-right: 15px;
    }

    &__text {
      color: #1b1c21;
      overflow: hidden;
      margin-left: 8px;
      max-height: 700px;
      transition: max-height 2s;
    }

    .hidden {
      max-height: 110px;
      transition: max-height 1s;
      @media (min-width: 769px) {
        max-height: 40px;
      }
    }

    .button-container {
      box-shadow: 0 -18px 20px 0 #faf8f7;
    }

    &__logo {
      margin-top: 10px;
      margin-bottom: 20px;
      width: 100%;
      border-radius: 5px;

      @media (min-width: 769px) {
        margin-right: 20px;
      }
    }

    @media (min-width: 769px) {
      display: flex;
      justify-content: space-between;
    }
  }

  .reader {
    display: flex;

    &__button {
      background-color: transparent;
      border: none;
      font-size: 16px;
    }
  }
}

.content {
  .button-container {
    background-color: #faf8f7;
    box-shadow: 0 -5px 20px 0 #faf8f7;
    position: relative;
    z-index: 1;
    padding: 10px 0 5px 10px;

    @media (min-width: 769px) {
      padding-bottom: 40px;
    }

    .open-button {
      gap: 15px;
      display: none;
      margin-left: 15px;
      @include button;
      width: 150px;

      @media (min-width: 769px) {
        display: flex;
      }
    }
  }
}

.brands {
  .brands-heading {
    color: rgba(126, 126, 130, 1);
    font-weight: 300;
    padding: 15px;
    border-top: 2px solid #d9fff5;
    margin-top: 0;
    background: linear-gradient(90deg, #f8f8f8 0%, #fff 98.61%);
  }

  .table-container {
    background-color: #f8f8f8;
    padding: 20px;

    .table {
      display: none;
      width: 100%;
      table-layout: auto;
      border-collapse: collapse;

      .rows {
        border-bottom: 1px solid #eaeaea;
        background-color: #ffffff;
        margin: 10px;

        &__cell {
          padding: 20px;
        }
      }

      .row--1 {
        border-bottom: none;
        background-color: #f8f8f8;
        color: #7e7e82;
      }

      .button-3 {
        @include button-3;
        position: static;
        display: flex;
        gap: 10px;
        border: none;

        &__text {
          font-size: 12px;
          font-weight: 700;
          color: white;
        }
      }

      @media (min-width: 769px) {
        display: table;
      }
    }
  }

  .swiper {
    padding: 20px;
    background-color: #faf8f7;
    mask-image: linear-gradient(to right, transparent 0%, black 10%, black 90%, transparent 100%);

    @media (min-width: 769px) {
      mask-image: none;
    }

    .swiper-pagination {
      @media (min-width: 769px) {
        display: none;
      }
    }
  }

  .swiper--3 {
    @media (min-width: 769px) {
      display: none;
    }
  }

  .swiper-wrapper {
    margin-bottom: 15px;

    @media (min-width: 769px) {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 15px;
      max-height: 700px;
      transition: max-height 1s;
      margin-bottom: 0;
    }

    @media (min-width: 1440px) {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }

  .hidden {
    @media (min-width: 769px) {
      max-height: 160px;
      transition: max-height 1s;
    }
  }

  .swiper-slide {
    width: 200px;
    height: auto;
    padding: 15px 15px 0 15px;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    background-color: #ffffff;
    padding-bottom: 10px;
    color: black;
    text-decoration: none;
    position: relative;

    @media (min-width: 769px) {
      width: auto;
    }

    .button {
      background: none;
      color: inherit;
      border: none;
      padding: 0;
      font: inherit;
      cursor: pointer;
      outline: inherit;

      .button__text {
        font-size: 16px;
      }

      .sprite-elem {
        color: #ff3e79;
        fill: white;
        stroke: #ff3e79;
      }
    }

    .button-3 {
      .sprite-elem {
        color: white;
        fill: #ff3e79;
        stroke: none;
      }
    }

    .button-swiper {
      display: flex;
      justify-content: space-between;
      width: 100%;

      &__arrow-2 {
        position: absolute;
        bottom: 10px;
        right: 10px;
      }
    }

    .button-3 {
      @include button-3;

      .button-3__text {
        font-size: 12px;
        font-weight: 700;
        color: white;
        font-family: $font-stack;
      }
    }

    .button-swiper__logo-six {
      width: 40px;
      height: 40px;
      overflow: hidden;
    }

    &:hover {
      border: 1px solid #41f6d7;
      transition: all 1s;

      .sprite-elem {
        color: #ffffff;
        fill: #ff3e79;
        transition: all 1s;
      }
    }
  }

  .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    background-color: #b5b6bc;
  }

  .button-container {
    background-color: #faf8f7;
    box-shadow: 0 -5px 20px 0 #faf8f7;
    position: relative;
    z-index: 1;
    padding: 10px 0 5px 10px;

    @media (min-width: 769px) {
      padding-bottom: 40px;
    }

    .open-button {
      gap: 15px;
      display: none;
      margin-left: 15px;
      @include button;
      width: 150px;

      @media (min-width: 769px) {
        display: flex;
      }
    }
  }

  .button-container-3 {
    @media (min-width: 769px) {
      display: none;
    }
  }

  .swiper-slide--2 {
    height: 150px;
  }

  .swiper-slide-3 {
    width: 260px;
    height: 200px;

    .swiper-slide-3__text {
      padding-bottom: 5px;
      margin-left: 5px;
      font-size: 14px;
      font-weight: 500;

      .swiper-slide-3__ligth-text {
        color: #7e7e82;
        display: inline-block;
        margin-bottom: 5px;
      }
    }
  }
}

.remark {
  background-color: #f8f8f8;
  padding-top: 15px;
  padding-bottom: 50px;

  &__text {
    margin-left: 20px;
    font-size: 14px;
    margin-right: 35px;
    max-width: 421px;
    line-height: 1.5;
    margin-top: 0;
  }

  .commerce {
    text-decoration: none;
    color: #1b1c21;
    font-weight: 700;
    font-size: 18px;
    margin-left: 20px;
    display: block;
    white-space: pre-wrap;

    &__more {
      position: relative;
      top: 0.5em;
    }

    &:hover {
      color: rgb(223, 50, 50);
      transition: 0.5s;
    }
  }
}

.footer {
  text-align: center;
  color: #7e7e82;
  background-color: #f8f8f8;
  border-top: 2px solid #d9fff5;
  padding: 8px;
  grid-area: footer;
}

.content {
  .info-reader {
    .open-button {
      display: flex;
      margin-left: 0;
    }
  }
}

.popup {
  position: fixed;
  top: 0;
  right: 0;
  background-color: white;
  padding: 24px;
  z-index: 4;
  box-sizing: border-box;
  transform: translateX(150%);
  transition: 1s;
  box-shadow:
    16px 0 52px 0 rgba(14, 24, 80, 0.2),
    -2px 0 4px 0 rgba(69, 79, 126, 0.02);

  &__element {
    position: relative;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: -20px;
      transform: translateY(-50%);
      width: 4px;
      height: 35px;
      background: #41f6d7;
      border-radius: 0 4px 4px 0;
    }
  }

  .button-3 {
    @include button-3;
    position: static;
    display: flex;
    gap: 10px;
    border: none;
    margin-left: auto;

    &__text {
      font-size: 12px;
      font-weight: 700;
      color: white;
    }

    .sprite-elem {
      color: white;
      fill: #ff3e79;
      stroke: none;
    }
  }
  &__red {
    color: red;
  }
  .close {
    @media (min-width: 769px) {
      position: absolute;
      left: -70px;
      top: 45px;
      z-index: 4;
      background: none;
    }
    @media (min-width: 769px) {
      display: inline-block;
    }
  }
}

.popup--open {
  transform: translateX(0);
  transition: 1s;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.96);
  backdrop-filter: blur(2px);
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  transition:
    opacity 0.5s,
    visibility 0.5s;
}

.overlay-2 {
  z-index: 3;
  transition:
    opacity 0.5s,
    visibility 0.5s;
}

.overlay--active {
  opacity: 1;
  visibility: visible;
}
