.sidebar {
  .button-side {
    .sprite-elem {
      color: white;
      fill: #ff3e79;

      &:hover {
        color: #ff3e79;
        fill: white;
        stroke: #ff3e79;
        transition: all 1s;
      }
    }
  }
}

.icons-button {
  .sprite-elem {
    color: white;
    fill: #ff3e79;

    &:hover {
      color: #ff3e79;
      fill: white;
      stroke: #ff3e79;
      transition: all 1s;
    }
  }
}

.button-3 {
  .sprite-elem {
    color: white;
    fill: #ff3e79;
  }
  &:hover {
    .sprite-elem {
      color: #ff3e79;
      fill: white;
      transition: all 1s;
    }

    .button-3__text {
      &::after {
        width: 100%;
        transition: 1s;
      }
    }
  }
  &__text {
    position: relative;
    &::after {
      position: absolute;
      content: '';
      bottom: -3px;
      left: 0;
      height: 2px;
      width: 0;
      background-color: white;
      transition: 1s;
    }
  }
}

.settings-icons__button {
